import { Component, OnInit } from '@angular/core';
import {MAT_TOOLTIP_DEFAULT_OPTIONS, MatTooltipDefaultOptions} from '@angular/material/tooltip';

/** Custom options the configure the tooltip's default show/hide delays. */
export const myCustomTooltipDefaults: MatTooltipDefaultOptions = {
  showDelay: 1000,
  hideDelay: 1000,
  touchendHideDelay: 1000,
};


@Component({
  selector: 'app-tooltip-with-hide-show-delay',
  templateUrl: './tooltip-with-hide-show-delay.component.html',
  styleUrls: ['./tooltip-with-hide-show-delay.component.css'],
  providers: [
    {provide: MAT_TOOLTIP_DEFAULT_OPTIONS, useValue: myCustomTooltipDefaults}
  ],
})
export class TooltipWithHideShowDelayComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {
  }

}
